@import url(fonts.css);

html,
body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

a {
  text-decoration: none;
}

p {
  font-family: "ProximaNovaRegular", Arial, sans-serif;
  font-size: 16px;
  line-height: 24px;
  color: #2a2323;
}

.btn {
  display: inline-block;
  font-size: 20px;
  font-family: "ProximaNovaSemibold", Arial, sans-serif;
  text-align: center;
  line-height: 50px;
  padding: 0 64px;
  border: 2px solid #ea282d;
  transform: skew(-10deg);
  background-color: transparent;
  color: #ea282d;
  position: relative;
  overflow: hidden;
  transition: color .5s;
}

.btn::after {
  position: absolute;
  content: "";
  background-color: #ea282d;
  width: 100%;
  height: 100%;
  left: -100%;
  top: 0;
  z-index: -1;
  transition: all .2s ease-in-out .2s;
}

.btn:hover {
  color: #fff;
  cursor: pointer;
}

.btn:hover::after {
  left: 0;
}

.btn span {
  display: inline-block;
  transform: skew(10deg);
}

.wrapper {
  width: 80%;
  margin: 0 auto;
  height: 100%;
}

.page-wrapper {
  width: 100%;
  height: 100%;
  overflow: hidden;
  background-color: #eaeae8;
}

.section {
  width: 100%;
  height: 100%;
  position: absolute;
}

.intro {
  display: flex;
  align-items: center;
  width: 100%;
  height: 100%;
  position: relative;
}

.intro .intro-image {
  width: 30%;
  text-align: center;
  position: absolute;
  top: 50%;
  right: 60px;
  transform: translate(0, -42%);
}

.intro .intro-image img {
  width: 100%;
  height: auto;
}

.intro-offer h1 {
  font-family: "GilroyExtraBold", Arial, sans-serif;
  font-size: 50px;
  text-transform: uppercase;
  line-height: 60px;
  margin-bottom: 30px;
  color: #272020;
}

.intro-offer h1 span {
  color: #ea282d;
}

.intro-offer__description {
  max-width: 380px;
  font-size: 20px;
  line-height: 30px;
  margin-bottom: 43px;
  color: #2a2323;
}

.product {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 100%;
  position: relative;
}

.discs-image {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 30%;
  margin: 0 12%;
}

.discs-image img {
  width: 100%;
  height: auto;
}

.product-offer {
  flex-grow: 1;
}

.product-offer__header {
  font-family: "ProximaNovaSemibold", Arial, sans-serif;
  font-size: 36px;
  line-height: 54px;
  padding-left: 30px;
  margin-top: 0;
  margin-bottom: 40px;
  position: relative;
  color: #272020;
}

.product-offer__header:before {
  position: absolute;
  content: '';
  width: 4px;
  height: 24px;
  left: 0;
  top: 14px;
  transform: rotate(15deg);
  background-color: #ea282d;
}

.product-offer__description {
  font-family: "ProximaNovaRegular", Arial, sans-serif;
  font-size: 16px;
  line-height: 24px;
  max-width: 460px;
  color: #453c3c;
  margin-bottom: 40px;
}

.product-offer__link {
  display: inline-block;
  font-family: "ProximaNovaSemibold", Arial, sans-serif;
  color: #ea282d;
  text-decoration: none;
  position: relative;
  text-align: center;
  transform: skew(-10deg);
  transition: padding .2s ease-in-out .2s;
}

.product-offer__link span {
  display: inline-block;
  transform: skew(10deg);
}

.product-offer__link::before {
  position: absolute;
  content: "";
  background-color: #ea282d;
  width: 3px;
  height: 20px;
  top: 0;
  left: -20px;
  opacity: 0;
  z-index: -1;
  transition: all .2s ease-in-out .2s;
}

.product-offer__link:hover {
  padding-left: 20px;
}

.product-offer__link:hover::before {
  opacity: 1;
  left: 0;
}

.product-link {
  display: inline-block;
  font-family: "ProximaNovaSemibold", Arial, sans-serif;
  color: #ea282d;
  text-decoration: none;
  margin-top: auto;
}

.pads-image {
  width: 50%;
}

.pads-image img {
  width: 100%;
  height: auto;
}

.product-items {
  display: flex;
  justify-content: space-between;
  max-width: 500px;
}

.product-item {
  display: flex;
  justify-content: space-between;
}

.product-item__description {
  max-width: 140px;
}

.product-item__description span {
  font-family: "ProximaNovaSemibold", Arial, sans-serif;
  font-size: 22px;
  line-height: 30px;
  display: inline-block;
  margin-bottom: 6px;
  color: #ea282d;
}

.video-content__wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.video-wrapper {
  width: 60%;
  position: relative;
  cursor: pointer;
}

.video-wrapper .video-description {
  bottom: 10%;
  left: -100px;
  position: absolute;
}

.video-wrapper .video-description .video-header {
  font-family: "ProximaNovaThin", Arial, sans-serif;
  font-size: 36px;
  color: #fff;
  line-height: 46px;
}

.video-wrapper .video-description .video-header span {
  font-family: "ProximaNovaSemibold", Arial, sans-serif;
}

.video-wrapper .video-description .header_description {
  font-size: 16px;
  color: #fff;
  font-family: "ProximaNovaThin", Arial, sans-serif;
}

.video-wrapper .play-button {
  width: 97px;
  height: 97px;
  background-color: #ea282d;
  border-radius: 50px;
  opacity: .8;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  cursor: pointer;
  z-index: 2;
  transition: box-shadow .5s;
}

.video-wrapper .play-button::after {
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  position: absolute;
  width: 140%;
  height: 140%;
  content: '';
  border: 2px dotted #fff;
  border-radius: 50%;
  background: transparent;
  animation: button-effect 2s linear infinite;
  cursor: pointer;
}

.video-wrapper .play-button .triangle {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.video-wrapper .play-button:hover {
  cursor: pointer;
  box-shadow: 0px 0px 50px 0px #d52026;
}

@keyframes button-effect {
  0% {
    transform: translate(-50%, -50%) scale(1);
  }

  50% {
    transform: translate(-50%, -50%) scale(1.3);
  }

  100% {
    transform: translate(-50%, -50%) scale(1);
  }
}

.myths {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.myths-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
}

.myths-item {
  box-sizing: border-box;
  width: 32%;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 360px;
  padding: 45px;
  padding-top: 90px;
  padding-bottom: 35px;
  justify-content: center;
}

.myths-item:nth-child(1) {
  background: url(../img/num-1.png) no-repeat #fff;
  background-position: right bottom;
}

.myths-item:nth-child(2) {
  background: url(../img/num-2.png) no-repeat #fff;
  background-position: right bottom;
}

.myths-item:nth-child(3) {
  background: url(../img/num-3.png) no-repeat #fff;
  background-position: right bottom;
}

.myths-item:hover {
  cursor: pointer;
}

.myths-item:hover::before {
  opacity: 1;
}

.myths-item:hover::after {
  opacity: 1;
}

.myths-item::before {
  left: 0;
  top: 0;
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #ea282d;
  opacity: 0;
  transition: .4s;
}

.myths-item::after {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #fff;
  content: '/ Find the truth';
  font-family: "ProximaNovaSemibold", Arial, sans-serif;
  font-size: 24px;
  line-height: 30px;
  opacity: 0;
  transition: .8s;
}

.myths-item__description {
  font-family: "ProximaNovaRegular", Arial, sans-serif;
  font-size: 16px;
  color: #272020;
  line-height: 24px;
  margin-top: 0;
}

.myths-item__header {
  font-family: "ProximaNovaSemibold", Arial, sans-serif;
  font-size: 24px;
  line-height: 30px;
  color: #272020;
  margin-top: 0;
}

.myths .product-offer__link {
  margin-top: auto;
  margin-bottom: 0;
}

.section-1 {
  background: url(../img/background-1.svg) #eaeae8 no-repeat center;
  background-size: 100%;
  opacity: 0;
  visibility: hidden;
  transition: opacity .4s ease-in-out .4s;
}

.section-2 {
  background: url(../img/background-2.svg) #eaeae8 no-repeat center;
  background-size: 100%;
  opacity: 0;
  visibility: hidden;
  transition: opacity .4s ease-in-out .4s;
}

.section-3 {
  background: url(../img/background-3.svg) #eaeae8 no-repeat center;
  background-size: 100%;
  opacity: 0;
  visibility: hidden;
  transition: opacity .4s ease-in-out .4s;
}

.section-4 {
  background: url(../img/video-bg.jpg) #382020 no-repeat center;
  background-size: cover;
  opacity: 0;
  visibility: hidden;
  transition: opacity .4s ease-in-out .4s;
}

.section-5 {
  background: url(../img/background-1.svg) #eaeae8 no-repeat center;
  background-size: 100%;
  background-color: #eaeae8;
  opacity: 0;
  visibility: hidden;
  transition: opacity .4s ease-in-out .4s;
}

.section-is-active {
  z-index: 2;
  opacity: 1;
  visibility: visible;
  transition: opacity .4s ease-in-out .4s;
}

.main-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  z-index: 1000;
  top: 0;
  width: 100%;
  height: 90px;
}

.main-header__logo {
  margin-left: 45px;
}

.mobile-burger {
  display: inline-block;
  position: relative;
  height: 30px;
  width: 30px;
  margin-right: 45px;
  transition: .2s;
}

.mobile-burger:hover {
  background-color: #ea282d;
  transform: skew(-10deg);
  box-shadow: 0 6px 10px rgba(0, 0, 0, 0.1);
}

.mobile-burger:hover span {
  background-color: #fff;
}

.mobile-burger:hover span::before {
  background-color: #fff;
}

.mobile-burger:hover span::after {
  background-color: #fff;
}

.mobile-burger span {
  width: 24px;
  height: 3px;
  margin-right: 45px;
  background-color: #272020;
  border-radius: 3px;
  bottom: 5px;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -12px;
  margin-top: -1px;
  transition: .2s;
}

.mobile-burger span::before {
  position: absolute;
  content: '';
  width: 24px;
  height: 3px;
  background-color: #272020;
  border-radius: 3px;
  transform: translateY(-8px);
  transition: .2s;
}

.mobile-burger span::after {
  position: absolute;
  content: '';
  width: 24px;
  height: 3px;
  background-color: #272020;
  border-radius: 3px;
  transform: translateY(8px);
  transition: .2s;
}

.main-menu {
  position: fixed;
  width: 350px;
  height: 100%;
  background-color: #fff;
  top: 0;
  right: -100%;
  z-index: 1999;
  padding: 40px 40px;
  box-sizing: border-box;
  transition: .5s ease-in-out;
}

.main-menu__active {
  right: 0;
}

.main-menu__list {
  margin: 0;
  padding-left: 0;
  width: 100%;
  height: 100%;
}

.main-menu__list li {
  list-style: none;
}

.main-menu .close-btn {
  display: inline-block;
  position: relative;
  height: 30px;
  width: 30px;
  transition: .2s;
}

.main-menu .close-btn:hover {
  background-color: #ea282d;
}

.main-menu .close-btn:hover::before {
  background-color: #fff;
}

.main-menu .close-btn:hover::after {
  background-color: #fff;
}

.main-menu .close-btn::before {
  position: absolute;
  top: 50%;
  left: 50%;
  content: '';
  width: 24px;
  height: 3px;
  background-color: #272020;
  border-radius: 3px;
  transform: translate(-50%, -50%) rotate(-45deg);
  transition: .2s;
  transform-origin: center center;
}

.main-menu .close-btn::after {
  position: absolute;
  content: '';
  top: 50%;
  left: 50%;
  width: 24px;
  height: 3px;
  background-color: #272020;
  border-radius: 3px;
  transform: translate(-50%, -50%) rotate(45deg);
  transition: .2s;
  transform-origin: center center;
}

.overlay {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 1998;
  background-color: #272020;
  opacity: .9;
  display: none;
  transition: .5s ease-in-out;
}

.overlay__active {
  display: block;
}

.menu-offer__header {
  font-family: "ProximaNovaSemibold", Arial, sans-serif;
  font-size: 36px;
  line-height: 54px;
  padding-left: 30px;
  margin-top: 40px;
  margin-bottom: 40px;
  position: relative;
  color: #272020;
}

.menu-offer__header:before {
  position: absolute;
  content: '';
  width: 4px;
  height: 24px;
  left: 0;
  top: 14px;
  transform: rotate(15deg);
  background-color: #ea282d;
}

.menu-phone {
  font-family: "ProximaNovaSemibold", Arial, sans-serif;
  font-size: 26px;
  line-height: 54px;
  padding: 30px;
  margin-top: 40px;
  margin-bottom: 40px;
  position: relative;
  color: #272020;
  transition: .5s ease-in-out;
}

.menu-phone:hover {
  background-color: #ea282d;
  color: #fff;
}

.site-description {
  font-family: "ProximaNovaRegular", Arial, sans-serif;
  text-transform: uppercase;
  font-size: 12px;
  color: #272020;
  left: 45px;
  top: 50%;
  z-index: 800;
  transform: translateX(-50%) rotate(-90deg);
  position: absolute;
}

.main-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  z-index: 1000;
  bottom: 0;
  width: 100%;
  height: 90px;
}

.main-footer__right {
  margin-left: 45px;
  font-family: "ProximaNovaRegular", Arial, sans-serif;
  font-size: 12px;
  color: #272020;
}

.main-footer__social {
  margin-right: 45px;
  display: flex;
  width: 100px;
  justify-content: space-between;
  align-items: center;
}

.main-footer__social a {
  width: 16px;
  height: 16px;
  display: flex;
  align-items: center;
  padding: 4px;
  transition: .5s ease-in-out;
}

.main-footer__social a:hover {
  opacity: .7;
}

.main-footer__social a.vk {
  width: 20px;
  height: 20px;
}

.social-icon svg {
  width: 100%;
  height: auto;
}

.slide-nav {
  position: absolute;
  right: 45px;
  top: 50%;
  z-index: 999;
  transform: translateY(-50%);
}

.slide-nav li {
  list-style: none;
}

.slide-nav__indicator {
  padding-right: 28px;
  position: relative;
  cursor: pointer;
}

.slide-nav__indicator:hover span {
  color: #ea282d;
  opacity: .4;
}

.slide-nav__indicator:hover::after {
  background-color: #ea282d;
  width: 4px;
  height: 4px;
  transform: translate(20%, -50%);
  background-color: transparent;
  border: 3px solid #ea282d;
  opacity: .4;
}

.slide-nav__indicator:after {
  position: absolute;
  content: '';
  display: block;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 4px;
  height: 4px;
  background-color: #ababab;
  border-radius: 50%;
  transition: all .2s ease-in-out;
}

.slide-nav__indicator span {
  font-family: "ProximaNovaSemibold", Arial, sans-serif;
  font-size: 20px;
  opacity: 0;
  transition: all .2s ease-in-out;
}

.slide-nav__indicator-is-active:after {
  background-color: #ea282d;
  width: 4px;
  height: 4px;
  transform: translate(20%, -50%);
  background-color: transparent;
  border: 3px solid #ea282d;
}

.slide-nav__indicator-is-active span {
  color: #ea282d;
  opacity: 1;
}

.hidden {
  display: none;
}

.white {
  color: #fff;
  transition: .4s;
}

.svg-white {
  fill: #fff;
}

.preloader {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 2000;
  width: 100%;
  height: 100%;
  background-color: #eaeae8;
  visibility: visible;
  opacity: 1;
  transition: 1s;
}

.preloader-item {
  width: 250px;
  height: 60px;
  background-color: transparent;
  position: absolute;
  text-align: center;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%) skew(-10deg);
  overflow: hidden;
  animation: 2s show;
}

.preloader-item::after {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  left: -100%;
  top: 0;
  background-color: #ea282d;
  z-index: -1;
  animation: 1.5s preloader 1s;
  animation-fill-mode: forwards;
}

.preloader-item span {
  cursor: default;
  font-family: 'GilroyExtraBold', sans-serif;
  color: #fff;
  font-size: 36px;
  line-height: 60px;
  width: 250px;
  height: 60px;
  transform: translate(-50%, -50%) skew(-10deg);
}

.preloader.done {
  visibility: hidden;
  opacity: 0;
}

@keyframes preloader {
  from {
    left: -100%;
  }

  to {
    left: 0;
  }
}

@keyframes show {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@media only screen and (max-width: 992px) {
  html,
  body {
    overflow: scroll;
    height: auto;
  }

  .page-wrapper {
    height: auto;
  }

  .section {
    position: relative;
  }

  .intro {
    flex-direction: column;
    min-height: 480px;
  }

  .intro .intro-image {
    position: static;
    transform: none;
    width: auto;
  }

  .intro-offer {
    margin-bottom: 80px;
    display: flex;
    flex-direction: column;
  }

  .product {
    flex-direction: column;
  }

  .discs-image {
    order: 2;
    width: 70%;
    margin-top: 70px;
  }

  .pads-image {
    width: 90%;
  }

  .pads-image img {
    width: 100%;
    height: auto;
  }

  .product-items {
    margin-bottom: 70px;
  }

  .myths-item {
    width: 48%;
    margin-bottom: 20px;
  }

  .section-1 {
    visibility: visible;
    opacity: 1;
    padding: 100px 0 0;
  }

  .section-2 {
    visibility: visible;
    opacity: 1;
    padding: 100px 0 0;
  }

  .section-3 {
    visibility: visible;
    opacity: 1;
    padding: 100px 0;
  }

  .section-4 {
    visibility: visible;
    opacity: 1;
    height: 400px;
  }

  .section-5 {
    visibility: visible;
    opacity: 1;
    padding: 100px 0;
  }

  .main-header {
    background-color: #eaeae8;
    box-shadow: 0 8px 8px rgba(0, 0, 0, 0.1);
    height: 60px;
  }

  .site-description {
    display: none;
  }

  .main-footer {
    display: none;
  }

  .slide-nav {
    display: none;
  }
}

@media only screen and (max-width: 576px) {
  .intro-offer .btn {
    align-self: center;
    width: 70%;
  }

  .discs-image img {
    width: 100%;
    height: auto;
  }

  .product-offer {
    margin-bottom: 50px;
  }

  .pads-image {
    width: 90%;
  }

  .product-items {
    flex-direction: column;
    justify-content: center;
  }

  .product-item__description {
    justify-content: center;
    max-width: 50%;
  }

  .product-item__description span {
    font-size: 28px;
  }

  .video-wrapper {
    width: 100%;
  }

  .video-wrapper .video-description {
    display: none;
  }

  .video-wrapper .play-button {
    width: 60px;
    height: 60px;
  }

  .myths {
    flex-direction: column;
  }

  .myths-item {
    width: 100%;
    margin-bottom: 50px;
  }

  .myths-item:nth-child(3) {
    margin-bottom: 0;
  }
}