@import "fonts.css";
@import "vars";

html,body {
	margin: 0;
	padding: 0;
	width: 100%;
	height: 100%;
	overflow: hidden;

	@media only screen and (max-width: 992px) {
		overflow: scroll;
		height: auto; } }

a {
	text-decoration: none; }

p {
	font-family: "ProximaNovaRegular", Arial, sans-serif;
	font-size: 16px;
	line-height: 24px;
	color: $middle-grey; }



.btn {
	display: inline-block;
	font-size: 20px;
	font-family: "ProximaNovaSemibold", Arial, sans-serif;
	text-align: center;
	line-height: 50px;
	padding: 0 64px;
	border: 2px solid $red;
	transform: skew(-10deg);
	background-color: transparent;
	color: $red;
	position: relative;
	overflow: hidden;
	transition: color .5s;

	&::after {
		position: absolute;
		content: "";
		background-color: $red;
		width: 100%;
		height: 100%;
		left: -100%;
		top: 0;
		z-index: -1;
		transition: all .2s ease-in-out .2s; }

	&:hover {
		color: #fff;
		cursor: pointer;

		&::after {
			left: 0; } }


	span {
		display: inline-block;
		transform: skew(10deg); } }


.wrapper {
	width: 80%;
	margin: 0 auto;
	height: 100%; }




.page-wrapper {
	width: 100%;
	height: 100%;
	overflow: hidden;
	background-color: $main-background;

	@media only screen and (max-width: 992px) {
		height: auto; } }


.section {
	width: 100%;
	height: 100%;
	position: absolute;

	@media only screen and (max-width: 992px) {
		position: relative; } }





.intro {
	display: flex;
	align-items: center;
	width: 100%;
	height: 100%;
	position: relative;



	@media only screen and (max-width: 992px) {
		flex-direction: column;
		min-height: 480px; }

	.intro-image {
		width: 30%;
		text-align: center;
		position: absolute;
		top: 50%;
		right: 60px;
		transform: translate(0, -42%);

		@media only screen and (max-width: 992px) {
			position: static;
			transform: none;
			width: auto; }

		img {
			width: 100%;
			height: auto; } } }




.intro-offer {

	@media only screen and (max-width: 992px) {
		margin-bottom: 80px;
		display: flex;
		flex-direction: column; }

	h1 {

		font-family: "GilroyExtraBold", Arial, sans-serif;
		font-size: 50px;
		text-transform: uppercase;
		line-height: 60px;
		margin-bottom: 30px;
		color: $dark-grey;

		span {
			color: $red; } }

	.btn {
		@media only screen and (max-width: 576px) {
			align-self: center;
			width: 70%; } }

	&__description {
		max-width: 380px;
		font-size: 20px;
		line-height: 30px;
		margin-bottom: 43px;
		color: $middle-grey; } }

.product {
	display: flex;
	justify-content: space-between;
	align-items: center;
	width: 100%;
	height: 100%;
	position: relative;

	@media only screen and (max-width: 992px) {
		flex-direction: column; } }


.discs-image {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 30%;
	margin: 0 12%;

	@media only screen and (max-width: 992px) {
		order: 2;
		width: 70%;
		margin-top: 70px; }

	img {
		width: 100%;
		height: auto;

		@media only screen and (max-width: 576px) {
			width: 100%;
			height: auto; } } }

.product-offer {
	flex-grow: 1;
	@media only screen and (max-width: 576px) {
		margin-bottom: 50px; }

	&__header {
		font-family: "ProximaNovaSemibold", Arial, sans-serif;
		font-size: 36px;
		line-height: 54px;
		padding-left: 30px;
		margin-top: 0;
		margin-bottom: 40px;
		position: relative;
		color: $dark-grey;

		&:before {
			position: absolute;
			content: '';
			width: 4px;
			height: 24px;
			left: 0;
			top: 14px;
			transform: rotate(15deg);
			background-color: $red; } }

	&__description {
		font-family: "ProximaNovaRegular", Arial, sans-serif;
		font-size: 16px;
		line-height: 24px;
		max-width: 460px;
		color: #453c3c;
		margin-bottom: 40px; }

	&__link {
		display: inline-block;
		font-family: "ProximaNovaSemibold", Arial, sans-serif;
		color: $red;
		text-decoration: none;
		position: relative;
		text-align: center;
		transform: skew(-10deg);
		transition: padding .2s ease-in-out .2s;

		span {
			display: inline-block;
			transform: skew(10deg); }


		&::before {
			position: absolute;
			content: "";
			background-color: $red;
			width: 3px;
			height: 20px;
			top: 0;
			left: -20px;
			opacity: 0;
			z-index: -1;
			transition: all .2s ease-in-out .2s; }

		&:hover {
			padding-left: 20px; }

		&:hover::before {
			opacity: 1;
			left: 0; } } }

.product-link {
	display: inline-block;
	font-family: "ProximaNovaSemibold", Arial, sans-serif;
	color: $red;
	text-decoration: none;
	margin-top: auto; }


.pads-image {
	width: 50%;

	@media only screen and (max-width: 992px) {
		width: 90%; }

	@media only screen and (max-width: 576px) {
		width: 90%; }

	img {
		width: 100%;
		height: auto;

		@media only screen and (max-width: 992px) {
			width: 100%;
			height: auto; } } }

.product-items {
	display: flex;
	justify-content: space-between;
	max-width: 500px;

	@media only screen and (max-width: 992px) {
		margin-bottom: 70px; }

	@media only screen and (max-width: 576px) {
		flex-direction: column;
		justify-content: center; } }

.product-item {
	display: flex;
	justify-content: space-between; }

.product-item__description {
	max-width: 140px;

	@media only screen and (max-width: 576px) {
		justify-content: center;
		max-width: 50%; }

	span {
		font-family: "ProximaNovaSemibold", Arial, sans-serif;
		font-size: 22px;
		line-height: 30px;
		display: inline-block;
		margin-bottom: 6px;
		color: $red;

		@media only screen and (max-width: 576px) {
			font-size: 28px; } } }

.video-content__wrapper {
	display: flex;
	justify-content: center;
	align-items: center;
	height: 100%; }

.video-wrapper {
	width: 60%;
	position: relative;
	cursor: pointer;

	@media only screen and (max-width: 576px) {
		width: 100%; }

	.video-description {
		bottom: 10%;
		left: -100px;
		position: absolute;

		@media only screen and (max-width: 576px) {
			display: none; }

		.video-header {
			font-family: "ProximaNovaThin", Arial, sans-serif;
			font-size: 36px;
			color: #fff;
			line-height: 46px;

			span {
				font-family: "ProximaNovaSemibold", Arial, sans-serif; } }

		.header_description {
			font-size: 16px;
			color: #fff;
			font-family: "ProximaNovaThin", Arial, sans-serif; } }

	.play-button {
		width: 97px;
		height: 97px;
		background-color: $red;
		border-radius: 50px;
		opacity: .8;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		cursor: pointer;
		z-index: 2;
		transition: box-shadow .5s;

		@media only screen and (max-width: 576px) {
			width: 60px;
			height: 60px; }



		&::after {
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			position: absolute;
			width: 140%;
			height: 140%;
			content: '';
			border: 2px dotted #fff;
			border-radius: 50%;
			background: transparent;
			animation: button-effect 2s linear infinite;
			cursor: pointer; }




		.triangle {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%); }

		&:hover {
			cursor: pointer;

			box-shadow: 0px 0px 50px 0px rgba(213,32,38,1); } } }



@keyframes button-effect {
	0% {
		transform: translate(-50%, -50%) scale(1); }
	50% {
		transform: translate(-50%, -50%) scale(1.3); }
	100% {
		transform: translate(-50%, -50%) scale(1); } }



.myths {
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;

	@media only screen and (max-width: 576px) {
		flex-direction: column; }

	&-wrapper {
		display: flex;
		flex-direction: column;
		justify-content: center;
		height: 100%; }


	&-item {
		box-sizing: border-box;
		width: 32%;
		position: relative;

		display: flex;
		flex-direction: column;
		justify-content: center;
		height: 360px;
		padding: 45px;
		padding-top: 90px;
		padding-bottom: 35px;
		justify-content: center;

		@media only screen and (max-width: 992px) {
			width: 48%;
			margin-bottom: 20px; }

		@media only screen and (max-width: 576px) {
			width: 100%;
			margin-bottom: 50px; }


		&:nth-child(1) {
			background: url(../img/num-1.png) no-repeat #fff;
			background-position: right bottom; }

		&:nth-child(2) {
			background: url(../img/num-2.png) no-repeat #fff;
			background-position: right bottom; }

		&:nth-child(3) {
			background: url(../img/num-3.png) no-repeat #fff;
			background-position: right bottom;

			@media only screen and (max-width: 576px) {
				margin-bottom: 0; } }

		&:hover {
			cursor: pointer;


			&::before {
				opacity: 1; }
			&::after {
				opacity: 1; } }

		&::before {
			left: 0;
			top: 0;
			content: '';
			position: absolute;
			width: 100%;
			height: 100%;
			background-color: $red;
			opacity: 0;
			transition: .4s; }

		&::after {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			color: #fff;
			content: '/ Find the truth';
			font-family: "ProximaNovaSemibold", Arial, sans-serif;
			font-size: 24px;
			line-height: 30px;
			opacity: 0;
			transition: .8s; }

		&__description {
			font-family: "ProximaNovaRegular", Arial, sans-serif;
			font-size: 16px;
			color: $dark-grey;
			line-height: 24px;
			margin-top: 0; }

		&__header {
			font-family: "ProximaNovaSemibold", Arial, sans-serif;
			font-size: 24px;
			line-height: 30px;
			color: $dark-grey;
			margin-top: 0; } }

	.product-offer__link {
		margin-top: auto;
		margin-bottom: 0; } }

.section-1 {
	background: url(../img/background-1.svg) $main-background no-repeat center;
	background-size: 100%;
	opacity: 0;
	visibility: hidden;
	transition: opacity .4s ease-in-out .4s;

	@media only screen and (max-width: 992px) {
		visibility: visible;
		opacity: 1;
		padding: 100px 0 0; } }


.section-2 {
	background: url(../img/background-2.svg) $main-background no-repeat center;
	background-size: 100%;
	opacity: 0;
	visibility: hidden;
	transition: opacity .4s ease-in-out .4s;

	@media only screen and (max-width: 992px) {
		visibility: visible;
		opacity: 1;
		padding: 100px 0 0; } }

.section-3 {
	background: url(../img/background-3.svg) $main-background no-repeat center;
	background-size: 100%;
	opacity: 0;
	visibility: hidden;
	transition: opacity .4s ease-in-out .4s;


	@media only screen and (max-width: 992px) {
		visibility: visible;
		opacity: 1;
		padding: 100px 0; } }

.section-4 {
	background: url(../img/video-bg.jpg) $video-background no-repeat center;
	background-size: cover;
	opacity: 0;
	visibility: hidden;
	transition: opacity .4s ease-in-out .4s;

	@media only screen and (max-width: 992px) {
		visibility: visible;
		opacity: 1;
		height: 400px; } }


.section-5 {
	background: url(../img/background-1.svg) $main-background no-repeat center;
	background-size: 100%;
	background-color: $main-background;
	opacity: 0;
	visibility: hidden;
	transition: opacity .4s ease-in-out .4s;

	@media only screen and (max-width: 992px) {
		visibility: visible;
		opacity: 1;
		padding: 100px 0; } }

.section-is-active {
	z-index: 2;
	opacity: 1;
	visibility: visible;
	transition: opacity .4s ease-in-out .4s; }

.main-header {
	display: flex;
	justify-content: space-between;
	align-items: center;
	position: fixed;
	z-index: 1000;
	top: 0;
	width: 100%;
	height: 90px;

	@media only screen and (max-width: 992px) {
		background-color: $main-background;
		box-shadow: 0 8px 8px rgba(0,0,0, .1);
		height: 60px; }

	&__logo {
		margin-left: 45px; } }


.mobile-burger {
	display: inline-block;
	position: relative;
	height: 30px;
	width: 30px;
	margin-right: 45px;
	transition: .2s;


	&:hover {

		background-color: $red;
		transform: skew(-10deg);
		box-shadow: 0 6px 10px rgba(0,0,0,.1);

		span {
			background-color: #fff; }

		span::before {
			background-color: #fff; }

		span::after {
			background-color: #fff; } }

	span {
		width: 24px;
		height: 3px;
		margin-right: 45px;
		background-color: $dark-grey;
		border-radius: 3px;
		bottom: 5px;
		position: absolute;
		top: 50%;
		left: 50%;
		margin-left: -12px;
		margin-top: -1px;
		transition: .2s;


		&::before {
			position: absolute;
			content: '';
			width: 24px;
			height: 3px;
			background-color: $dark-grey;
			border-radius: 3px;
			transform: translateY(-8px);
			transition: .2s; }

		&::after {
			position: absolute;
			content: '';
			width: 24px;
			height: 3px;
			background-color: $dark-grey;
			border-radius: 3px;
			transform: translateY(8px);
			transition: .2s; } } }

.mobile-burger__active {}



.main-menu {
	position: fixed;
	width: 350px;
	height: 100%;
	background-color: #fff;
	top: 0;
	right: -100%;
	z-index: 1999;
	padding: 40px 40px;
	box-sizing: border-box;
	transition: .5s ease-in-out;

	&__active {
		right: 0; }

	&__list {
		margin: 0;
		padding-left: 0;
		width: 100%;
		height: 100%;

		li {
			list-style: none; } }

	.close-btn {
		display: inline-block;
		position: relative;
		height: 30px;
		width: 30px;
		transition: .2s;

		&:hover {
			background-color: $red;

			&::before {
				background-color: #fff; }

			&::after {
				background-color: #fff; } }

		&::before {
			position: absolute;
			top: 50%;
			left: 50%;
			content: '';
			width: 24px;
			height: 3px;
			background-color: $dark-grey;
			border-radius: 3px;
			transform: translate(-50%, -50%) rotate(-45deg);
			transition: .2s;
			transform-origin: center center; }

		&::after {
			position: absolute;
			content: '';
			top: 50%;
			left: 50%;
			width: 24px;
			height: 3px;
			background-color: $dark-grey;
			border-radius: 3px;
			transform: translate(-50%, -50%) rotate(45deg);
			transition: .2s;
			transform-origin: center center; } } }


.overlay {
	position: fixed;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	z-index: 1998;
	background-color: $dark-grey;
	opacity: .9;
	display: none;
	transition: .5s ease-in-out;

	&__active {
		display: block; } }

.menu-offer__header {
	font-family: "ProximaNovaSemibold", Arial, sans-serif;
	font-size: 36px;
	line-height: 54px;
	padding-left: 30px;
	margin-top: 40px;
	margin-bottom: 40px;
	position: relative;
	color: $dark-grey;

	&:before {
		position: absolute;
		content: '';
		width: 4px;
		height: 24px;
		left: 0;
		top: 14px;
		transform: rotate(15deg);
		background-color: $red; } }

.menu-phone {
	font-family: "ProximaNovaSemibold", Arial, sans-serif;
	font-size: 26px;
	line-height: 54px;
	padding: 30px;
	margin-top: 40px;
	margin-bottom: 40px;
	position: relative;
	color: $dark-grey;
	transition: .5s ease-in-out;

	&:hover {
		background-color: $red;
		color: #fff; } }


.site-description {
	font-family: "ProximaNovaRegular", Arial, sans-serif;
	text-transform: uppercase;
	font-size: 12px;
	color: $dark-grey;
	left: 45px;
	top: 50%;
	z-index: 800;
	transform: translateX(-50%) rotate(-90deg);
	position: absolute;

	@media only screen and (max-width: 992px) {
		display: none; } }


.main-footer {
	display: flex;
	justify-content: space-between;
	align-items: center;
	position: fixed;
	z-index: 1000;
	bottom: 0;
	width: 100%;
	height: 90px;

	@media only screen and (max-width: 992px) {
		display: none; }

	&__right {
		margin-left: 45px;
		font-family: "ProximaNovaRegular", Arial, sans-serif;
		font-size: 12px;
		color: $dark-grey; }

	&__social {
		margin-right: 45px;
		display: flex;
		width: 100px;
		justify-content: space-between;
		align-items: center;

		a {
			width: 16px;
			height: 16px;
			display: flex;
			align-items: center;
			padding: 4px;
			transition: .5s ease-in-out;

			&:hover {
				opacity: .7; }

			&.vk {
				width: 20px;
				height: 20px; } } } }



.social-icon svg {
	width: 100%;
	height: auto; }

.slide-nav {
	position: absolute;
	right: 45px;
	top: 50%;
	z-index: 999;
	transform: translateY(-50%);

	@media only screen and (max-width: 992px) {
		display: none; }

	li {
		list-style: none; }

	&__indicator {
		padding-right: 28px;
		position: relative;
		cursor: pointer;

		&:hover {
			span {
				color: $red;
				opacity: .4; }


			&::after {
				background-color: $red;
				width: 4px;
				height: 4px;
				transform: translate(20%, -50%);
				background-color: transparent;
				border: 3px solid $red;
				opacity: .4; } }

		&:after {
			position: absolute;
			content: '';
			display: block;
			right: 0;
			top: 50%;
			transform: translateY(-50%);
			width: 4px;
			height: 4px;
			background-color: $nav-color;
			border-radius: 50%;
			transition: all .2s ease-in-out; }

		span {
			font-family: "ProximaNovaSemibold", Arial, sans-serif;
			font-size: 20px;
			opacity: 0;
			transition: all .2s ease-in-out; }


		&-is-active {

			&:after {
				background-color: $red;
				width: 4px;
				height: 4px;
				transform: translate(20%, -50%);
				background-color: transparent;
				border: 3px solid $red; }

			span {

				color: $red;
				opacity: 1; } } } }

.hidden {
	display: none; }

.white {
	color: #fff;
	transition: .4s; }

.svg-white {
	fill: #fff; }

.preloader {
	position: fixed;
	left: 0;
	top: 0;
	z-index: 2000;
	width: 100%;
	height: 100%;
	background-color: $main-background;
	visibility: visible;
	opacity: 1;
	transition: 1s;

	&-item {
		width: 250px;
		height: 60px;
		background-color: transparent;
		position: absolute;
		text-align: center;
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%) skew(-10deg);
		overflow: hidden;
		animation: 2s show;

		&::after {
			content: '';
			position: absolute;
			width: 100%;
			height: 100%;
			left: -100%;
			top: 0;
			background-color: $red;
			z-index: -1;
			animation: 1.5s preloader 1s;
			animation-fill-mode: forwards; }

		span {
			cursor: default;
			font-family: 'GilroyExtraBold', sans-serif;
			color: #fff;
			font-size: 36px;
			line-height: 60px;
			width: 250px;
			height: 60px;
			transform: translate(-50%, -50%) skew(-10deg); } }

	&.done {
		visibility: hidden;
		opacity: 0; } }

@keyframes preloader {
	from {
		left: -100%; }
	to {
		left: 0; } }

@keyframes show {
	from {
		opacity: 0; }
	to {
		opacity: 1; } }
